// Import dependencies
import { unsafeCSS, css, html, LitElement } from 'lit';
import { property, customElement } from 'lit/decorators.js';
import { confirm, isEmpty } from '@automator/utilities';
import apiFetch from '@wordpress/api-fetch';

// Add the nonce as middleware. This automatically adds the `X-WP-Nonce` header to requests.
apiFetch.use( apiFetch.createNonceMiddleware( UncannyAutomatorBackend.rest.nonce ) );

// Import style for web component
import styles from './web-component-style.scss';

// eslint-disable-next-line require-jsdoc
@customElement( 'uap-log-item-action-cancel-button' )
export class AutomatorLogActionCancelButton extends LitElement {
	// Define style
	static styles = css`${ unsafeCSS( styles ) }`;

	// Properties
	@property( { type: Number, attribute: 'item-id' } ) accessor itemID;

	@property( { type: Number, attribute: 'item-log-id' } ) accessor itemLogID;

	@property( { type: String, attribute: false } ) accessor fatalErrorMessage;

	@property( { type: Boolean } ) accessor loading = false;

	// This property is used to indicate to the web component that it shouldn't try to retrieve data from the server and that it should use the data provided when it was created
	@property( { type: Boolean, attribute: 'import-mode' } ) accessor isImportMode = false;

	// eslint-disable-next-line require-jsdoc
	render() {
		return html`
			${ this._templateButton }
		`;
	}

	/**
	 * Handles the cancel functionality
	 */
	async handleCancelRequest() {
		// Check if it's loading, then abort
		if ( this.loading ) {
			return;
		}

		// Show confirmation dialog
		await confirm( {
			content: html`
				<uo-alert type="warning">
					${ wp.i18n.__( 'Are you sure you want to cancel this action?', 'uncanny-automator' ) }
				</uo-alert>
			`
		} );

		// Do the request
		this.doRequest();
	}

	/**
	 * Sends an asynchronous request to cancel an action.
	 * This method sets the loading animation, clears any fatal error messages, and sends a POST request to the backend to cancel the action. It uses the `apiFetch` function to send the request with the necessary data.
	 * Once the request is completed, it stops the loading animation.
	 */
	doRequest() {
		// As soon as the call starts, set the loading animation
		this.loading = true;

		apiFetch( {
			url: `${ UncannyAutomatorBackend.rest.url }/async_cancel_action`,
			method: 'POST',
			data: {
				// eslint-disable-next-line camelcase
				item_log_id: this.itemLogID,
				item_id: this.itemID
			}
		} )
			.then( ( response ) => {
				// Stop loading animation
				this.loading = false;

				// Request data refresh
				this.requestDataRefresh();
			} )
			.catch( async error => {
				// Stop loading animation
				this.loading = false;

				// Request data refresh
				this.requestDataRefresh();

				// Show the error in the console
				console.error( error );

				// Get the error message
				const fatalErrorMessage = ! isEmpty( error.message ) ? error.message : wp.i18n.__( 'Unknown error', 'uncanny-automator' );

				// Show the error, and offer the user the option to retry
				await confirm( {
					heading: wp.i18n.__( 'Something went wrong', 'uncanny-automator' ),
					content: html`
						<uo-alert type="error">
							${ fatalErrorMessage }
						</uo-alert>
					`,
					confirmButtonLabel: wp.i18n.__( 'Try again', 'uncanny-automator' ),
					cancelButtonLabel: wp.i18n.__( 'Close', 'uncanny-automator' )
				} );

				// Try again
				this.doRequest();
			} );
	}

	/**
	 * Send request to main component to refresh the data
	 */
	requestDataRefresh() {
		this.dispatchEvent(
			new CustomEvent( 'uap-log-request-data-refresh', {
				bubbles: true,
				composed: true,
				detail: {
					// The ID used as the sidebar ID
					requestedBy: `item-${ this.itemID }`,
				}
			} )
		);
	}

	/**
	 * Creates the cancel button
	 * It'll create a disabled button if it's import mode
	 *
	 * @return {TemplateResult} The cancel button
	 */
	get _templateButton() {
		// Check if it's import mode
		if ( this.isImportMode ) {
			return html`
				<uo-tooltip>
					${ wp.i18n.__( 'Cancelling is not possible in import mode', 'uncanny-automator' ) }

					<uo-button
						slot="target"
						size="small"
						color="danger"
						disabled
					>
						<uo-icon id="times"></uo-icon> ${ wp.i18n.__( 'Cancel', 'uncanny-automator' ) }
					</uo-button>
				</uo-tooltip>
			`;
		}

		return html`
			<uo-button
				size="small"
				color="danger"

				?loading=${ this.loading }

				@click=${ this.handleCancelRequest }
			>
				<uo-icon id="times"></uo-icon> ${ wp.i18n.__( 'Cancel', 'uncanny-automator' ) }
			</uo-button>
		`;
	}
}
