/**
 * `markdown-parser` is a web component that parses and sanitizes markdown content from slotted content, allowing only specific HTML tags based on an attribute.
 *
 * @property {String} allowedTags - A comma-separated list of allowed HTML tags (e.g., "strong, em").
 */

import { unsafeCSS, css, html, LitElement } from 'lit';
import { customElement, property, state } from 'lit/decorators.js';
import markdownIt from 'markdown-it';
import { unsafeHTML } from 'lit-html/directives/unsafe-html.js';

// Import style for web component
import styles from './web-component-style.scss';

@customElement( 'uo-markdown' )
class AutomatorMarkdownParser extends LitElement {
	// Define style
	static styles = css`${ unsafeCSS( styles ) }`;

	// Properties

	/**
	 * A comma-separated string of allowed HTML tags.
	 * Example: "strong,em"
	 * @type {String}
	 */
	@property( { type: String, attribute: 'allowed-tags' } ) accessor allowedTags = 'strong,em';

	/**
	 * The content of the markdown to parse.
	 * @type {String}
	 */
	@property( { type: String } ) accessor content;

	// Initialize markdown-it with HTML disabled
	md = markdownIt( { html: false } );

	/**
	 * The render function renders the slot, and then the updated lifecycle method will handle the rest.
	 *
	 * @return {TemplateResult} The slotted content.
	 */
	render() {
		return this._templateParsedContent;
	}

	get _templateParsedContent() {
		// If there is no content, return nothing
		if ( ! this.content ) {
			return;
		}

		// Convert the entire text content to HTML using markdown-it
		let parsedHTML = this.convertMarkdownToHTML( this.content );

		// Check if we have to filter the HTML based on allowedTags
		if ( this.allowedTags ) {
			// Filter the HTML based on allowedTags
			parsedHTML = this.filterHTMLTags( parsedHTML );
		}

		return html`${ unsafeHTML( parsedHTML ) }`;
	}

	/**
	 * Converts the extracted plain text to HTML using the markdown-it library.
	 *
	 * @param {String} content - The plain text content to convert.
	 * @return {String} The HTML generated from the markdown.
	 */
	convertMarkdownToHTML( content ) {
		return this.md.render( content );
	}

	/**
	 * Filters the HTML based on allowed tags, removing any tags not in the allowedTags list.
	 *
	 * @param {String} htmlContent - The HTML content to filter.
	 * @return {String} The sanitized HTML.
	 */
	filterHTMLTags( htmlContent ) {
		const parser = new DOMParser();
		const doc = parser.parseFromString( htmlContent, 'text/html' );
		const allowedTagsArray = this.allowedTags.split( ',' ).map( tag => tag.trim() );

		this.recursivelyFilterTags( doc.body, allowedTagsArray );

		return doc.body.innerHTML;
	}

	/**
	 * Recursively filters the child elements of a given DOM element, removing any tags that are not in the allowedTags array.
	 *
	 * @param {HTMLElement} element - The root element to filter.
	 * @param {String[]} allowedTags - An array of allowed tag names.
	 */
	recursivelyFilterTags( element, allowedTags ) {
		const children = Array.from( element.childNodes );

		children.forEach( child => {
			if ( child.nodeType === Node.ELEMENT_NODE ) {
				if ( ! allowedTags.includes( child.tagName.toLowerCase() ) ) {
					child.replaceWith( ...Array.from( child.childNodes ) );
				} else {
					this.recursivelyFilterTags( child, allowedTags );
				}
			}
		} );
	}
}
