import { AutomatorTemplateItem } from '@pages/template-library/components/template-item/_base';
import { unsafeCSS, css, html } from 'lit';
import { getDelaySentence } from '@pages/template-library/utilities';
import { customElement, property } from 'lit/decorators.js';

// Import style for web component
import styles from './web-component-style.scss';

// eslint-disable-next-line require-jsdoc
@customElement( 'uap-template-item-action' )
export class AutomatorTemplateAction extends AutomatorTemplateItem {
	static styles = css`${ unsafeCSS( styles ) }`;

	/**
	 * The delay/schedule data of this action
	 *
	 * @type {Object}
	 * @property {Object} delay - Represents the code of the integration.
	 */
	@property( { type: Object, attribute: false } )
	accessor delay;

	/**
	 * The template with the delay/schedule data of this action
	 *
	 * @type {TemplateResult|undefined} The delay/schedule data of this action
	 */
	get _templateDelay() {
		if ( _uo.utility.isEmpty( this.delay ) ) {
			return;
		}

		return html`
			<div class="item__delay">
				<uo-icon id="clock"></uo-icon>
				${ getDelaySentence( this.delay ) }
			</div>
		`;
	}

	/**
	 * Renders the item sentence
	 *
	 * @return {TemplateResult} The sentence
	 */
	get _templateSentence() {
		return html`
			<div class="item__sentence">
				<uap-template-sentence .sentence="${ this.titleHTML }"></uap-template-sentence>
				${ this._templateDelay }
			</div>
		`;
	}
}
